.header {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#3c6eb5+0,009788+100&0.8+0,0.6+100 */
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, #447bb6 0%, #2695b1 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: -webkit-linear-gradient(20deg, #447bb6 0%, #2695b1 100%);
  background: -o-linear-gradient(20deg, #447bb6 0%, #2695b1 100%);
  background: linear-gradient(70deg, #447bb6 0%, #2695b1 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  height: 100vh;
  position: relative; }
  .header canvas {
    position: absolute;
    bottom: 0; }
  .header .header-content {
    width: 80%;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateY(-50%) translateX(-50%);
        -ms-transform: translateY(-50%) translateX(-50%);
            transform: translateY(-50%) translateX(-50%); }
  .header img.logo {
    /*position: absolute;
      top:38%;
      left:50%;
      transform:translateX(-50%);*/
    width: 220px; }
  .header img.mini-logo {
    position: absolute;
    top: 40px;
    left: 80px;
    width: 80px; }
  .header img.menu_btn {
    position: absolute;
    bottom: 30px;
    right: 30px;
    width: 25px;
    cursor: pointer; }
  .header img.scroll {
    position: absolute;
    bottom: 30px;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 20px;
