    display: flex; }
    .header .socials .social-single {
      margin-left: 20px; }
  .header .langs {
    position: absolute;
    top: 30px;
    right: 30px;
    width: 90px;
    height: 45px; }
    .header .langs p.langs__ {
      float: left;
      margin-right: 10px;
      opacity: 0.5; }
      .header .langs p.langs__.active {
        opacity: 1; }
  .header h2 {
    color: #fff;
    font-family: 'Lato', sans-serif;
    font-size: 30px;
    font-weight: 400;
    line-height: 36px;
    margin-bottom: 40px; }
    @media (max-width: 600px) {
      .header h2 {
        font-size: 22px; } }
  .header p {
    color: #fff;
    font-family: 'Lato', sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 27px; }
    .header p a {
      color: #fff;
      font-family: 'Lato', sans-serif;
      font-size: 18px;
      font-weight: 400;
      line-height: 27px;
      text-decoration: none; }
    @media (max-width: 600px) {
      .header p {
        font-size: 16px; } }



